//引入安装的axios插件
import axios from 'axios'
import App from './../App'
import vue from 'vue'
import common from '@/libs/units.js'

//请求Mock地址封装
axios.defaults.timeout = 100000;
//请求方法封装
const http = options => {
  var that = this;
  return new Promise((resolve, reject) => {
    const defaultOptions = {};
    const newOptions = {
      ...defaultOptions,
      ...options
    };
    var token = common.getCookie("accessToken");
    console.log("token------",token);
    if(token == null){
      window.location.href = 'https://pxtadmin.com/'
    }
    console.log("token-----",token);
    token = 'bearer'+' '+token;
    // var token = 'Bearer eyJhbGciOiJSUzI1NiIsImtpZCI6Ii1PZUVVR0NfZlFqa0U3b0dZZHhrX0EiLCJ0eXAiOiJhdCtqd3QifQ..f49klog4wUR_jiIMYP1C82Hhbs0u-mc8eIMZUtne3B-GIFXnuEDMreHcIMD3APj07QhFaXl3GDHP9GCOJ8WmLgwNwhKF1n8a1nvSUmK_j_zbKWjDeBF12Zid4c1HXbSQ6dc_qHpbxbTDNUsZABuz5pi8jRBJprjJHrvnzkLbur_iZRi6oFmktJD7x9wOTr6kVuOnqMh7otdPXgEtCblBbhceP5PhCA33O7ecPp2XxTInMN-Pe8KWkGqgO2l3v7tXuJdxok-BB7WhcJA4RUCBi41QMX3o1MjkDY8gQGPDKvZ2S5S3pOIFFItUgmV9CyhCuKz2ICHWVTw5uFMWQLzxDw';
    //headers默认传递json格式数据，这里也可以设置token，每次调用都会携带
    newOptions.headers = {
      // 'content-Type': 'application/json;charset=UTF-8',
      "authorization":token,
      // "deviceToken":token,
      ...newOptions.headers,
    };
    //这里可以在调用的时候看到你的method、url、data、headers等参数
    axios({
      method: newOptions.method,
      url: newOptions.url,
      data: newOptions.data,
      headers: newOptions.headers,
      dataType: 'JSONP',
    }).then(res => {
      if (res.status == 200) {
       //这里我们只需要获取返回的data中的数据即可
        resolve(res.data);
        if(res.data.code==90001){
          console.log(res.data.message);
          vue.prototype.$msgBox.showMsgBox({
            title: '您的账号被锁定，请解锁!',
            isShowInput: true
          }).then(async (val) => {
              // console.log(val);
          }).catch(() => {
              // ...
          });
        }else if(res.data.code== 1001){
          window.location.href = 'https://pxtadmin.com/'
        }
      }else {
        reject(res);
      }
    }).catch(err => {
      reject(err);
    })
  })
};
export default http
